      250deg,
      rgba(255, 135, 65, 1) 15%,
      rgba(255, 65, 65, 1) 65%
    );
  }
  
  .sign {
    width: 100%;
    transition-duration: 0.5s;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .delete-button:hover span.material-symbols-outlined {
